.fullWidth {
    position: relative;
}

.fullWidth::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    background-size: cover;
    background-position: 50% 50%;
    background-attachment: fixed;
}

.fullWidth_index .mainTitle {
    font-size: 72px;
}

.fullWidth_index {
    padding-bottom: 500px;
    @media(max-width:767px) {
        padding-bottom: 300px;
    }
}

.fullWidth_index::before {
    background-image: url('../img/fullWidth/index.webp');
    height: 500px;
    @media(max-width:767px) {
        height: 300px;
        background-image: url('../img/fullWidth/index_mob.webp');
    }
}

.fullWidth_index2 {
    padding-bottom: 800px;
    @media(max-width:767px) {
        padding-bottom: 400px;
    }
}

.fullWidth_index2::before {
    background-image: url('../img/fullWidth/index2.webp');
    height: 800px;
    @media(max-width:767px) {
        height: 400px;
        background-image: url('../img/fullWidth/index2_mob.webp');
    }
}

.fullWidth_meditation {
    padding-bottom: 600px;

    @media(max-width:767px) {
        padding-bottom: 300px;
    }
}

.fullWidth_meditation::before {
    background-image: url('../img/fullWidth/meditation.webp');
    height: 600px;

    @media(max-width:767px) {
        height: 300px;
        background-image: url('../img/fullWidth/meditation_mob.webp');
    }
}


.fullWidth_fromZero {
    padding-bottom: 600px;

    @media(max-width:767px) {
        padding-bottom: 300px;
    }
}

.fullWidth_fromZero::before {
    background-image: url('../img/fullWidth/fromZero.webp');
    height: 600px;

    @media(max-width:767px) {
        height: 300px;
        background-image: url('../img/fullWidth/fromZero_mob.webp');
    }
}

.fullWidth_fromZero {
    .mainTitle {
        margin-bottom: 10px;
        text-align: center !important;
    }

    .fullWidth__body {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        margin-bottom: 80px;

        @media(max-width:767px) {
            margin-bottom: 40px;
        }
    }

    .btn {
        width: 100%;
        max-width: 560px;
        margin-top: 20px;
    }
}


.fullWidth__txt {
    margin-top: 20px;
}

.fullWidth .mainTitle {
    text-align: left;
}

.fullWidth__sign {
    text-align: right;
    font-family: var(--third-family);
    font-size: 74px;
    color: #3a4143;
}

@media(max-width:1023px) {
    .fullWidth_index .mainTitle {
        font-size: 30px;
    }

    .fullWidth__sign {
        font-size: 30px;
    }

    .fullWidth_index {
        padding-bottom: 190px;
    }

    .fullWidth_index::before {
        background-image: url('../img/fullWidth/index_mob.webp');
        height: 190px;
    }

    .fullWidth_index2 {
        padding-bottom: 250px;
    }

    .fullWidth_index2::before {
        background-image: url('../img/fullWidth/index2_mob.webp');
        height: 250px;
    }

}