.meditationForYou {
    &__body {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 30px;
        gap: 24px;
        text-align: center;
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
    }

    &__item {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    &__p {
        margin-top: 20px;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 32px;
        text-align: center;
        color: #202020;
    }
}